body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background: linear-gradient(135deg, #1a2a44, #0d1b2a);
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
}

h1 {
  color: #00c4ff;
  text-align: center;
  margin-bottom: 30px;
  font-size: 2.5em;
  text-shadow: 2px 2px 6px rgba(0, 0, 0, 0.7);
  animation: fadeIn 1.5s ease-in-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-20px); }
  to { opacity: 1; transform: translateY(0); }
}

.chat-container {
  background: white;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.8);
  width: 500px;
  max-width: 90%; /* âœ… Shrinks on small screens */
  text-align: center;
  backdrop-filter: blur(5px);
  border: 1px solid #00c4ff;
}

input[type="text"] {
  padding: 12px;
  width: 70%;
  margin-right: 10px;
  border: none;
  border-radius: 5px;
  background: #ffffff;
  color: #000000;
  font-size: 1em;
  outline: none;
  transition: all 0.3s ease;
}

input[type="text"]::placeholder {
  color: #555555;
}

input[type="text"]:focus {
  background: #ffffff;
  box-shadow: 0 0 5px #00c4ff;
}

button {
  padding: 12px 25px;
  background: #00c4ff;
  border: none;
  border-radius: 5px;
  color: #ffffff;
  font-size: 1em;
  cursor: pointer;
  transition: all 0.3s ease;
}

button:hover {
  background: #0099cc;
  transform: scale(1.05);
  box-shadow: 0 0 10px #00c4ff;
}

.response {
  margin-top: 20px;
  font-size: 1.1em;
  color: #000000;
  line-height: 1.5;
  padding: 15px;
  border-radius: 5px;
  background: #ffffff;
  animation: slideUp 0.5s ease-out;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
}

@keyframes slideUp {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.response.error {
  color: #ff6b6b;
  background: #2a1b1b;
  border-left: 4px solid #ff6b6b;
}

/* ======== Mobile Styles ======== */
@media (max-width: 768px) {
  body {
    flex-direction: column;
    padding: 0;
    align-items: stretch;
  }

  .layout {
    flex-direction: column;
    margin: 0;
  }

  /* Mobile Header Bar */
  .mobile-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #0d1b2a;
    padding: 12px 15px;
    font-size: 1.3em;
    font-weight: bold;
    color: white;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    z-index: 20;
  }

  .hamburger {
    font-size: 1.5em;
    cursor: pointer;
  }

  /* Sidebar becomes sliding panel */
  .sidebar {
    position: fixed;
    top: 0;
    left: -260px;
    height: 100%;
    width: 240px;
    background: #0d1b2a;
    transition: left 0.3s ease;
    z-index: 15;
  }

  .sidebar.open {
    left: 0;
  }

  /* Full-width chat area */
  .chat-container {
    width: 100%;
    max-width: 95%;
    margin: 0 auto;
    padding: 20px;
  }

  input[type="text"] {
    width: 100%;
    margin: 0 0 10px 0;
  }

  button {
    width: 100%;
  }
}
